/* eslint-disable */
import axios from 'axios';
import AccountService from '@/service/AccountService';

import User from '@/models/User';
export default class RegisterViewModel {
   firstName = '';
   lastName = '';
   username = '';
   email = '';
   password = '';
   confirmPassword = '';
   dob = '';
   gender = -1;
   areaCode = '';
   phone = '';
   address = '';

   register(): void {
      //

      const newUser: User = {
         userName: this.username.trim(),
         password: this.password.trim(),
         email: this.email.trim(),
         firstName: this.firstName.trim(),
         lastName: this.lastName.trim(),
         address: this.address.trim(),
         phoneNumber: this.phone.trim(),
      };

      const registerValidation = new RegisterValidation();

      //   All input fields are not empty (is OK)
      if (!registerValidation.checkInputFieldsEmpty(newUser, this.dob.trim())) {
         //
         const validEmail = registerValidation.checkValidEmail(newUser.email);

         const validPassword = registerValidation.checkValidPassword(this.password.trim(), this.confirmPassword.trim());

         const validPhone = registerValidation.checkValidPhone(newUser.phoneNumber);

         // All conditions are valid -> send account to server
         if (validEmail && validPassword && validPhone) {
            newUser.dob = new Date(this.dob.trim());
            newUser.gender = this.gender;

            console.log(newUser); // For debug purpose

            AccountService.register(newUser);
         }
      }
   }
}

class RegisterValidation {
   /**
    * [This function use for validate password (Include check equal password and confirm password)]
    * @param  {[string]} password
    * @param  {[string]} confirmPassword
    * @return {[boolean]}      [return validation status (true or false)]
    */
   checkValidPassword(password: string, confirmPassword: string): boolean {
      let errorMsg = '';
      let validPassword = true;

      //   Invalid password length
      if (password.length < 8 || password.length > 31) {
         errorMsg += 'Password length must in range 8 and 31 characters\n';
         validPassword = false;
      }

      //   Password and confirm password are not equal
      if (password !== confirmPassword) {
         errorMsg += 'Password and confirm Password are not equal';
         validPassword = false;
      }

      //   Alert when invalid password
      if (errorMsg) alert(errorMsg);

      return validPassword;
   }

   /**
    * [This function use to find the empty input fields. Gender and DOB are splited from newUser then validate them later]
    * @param  {[User]} newUser
    * @param  {[string]} dob
    * @return {[boolean]}      [return validation status (true or false)]
    */
   checkInputFieldsEmpty(newUser: User, dob: string): boolean {
      let validInputfields = false;

      //   Find the empty fields
      for (let key in newUser) {
         // Empty fields detection
         if (!String(newUser[key]).toString()) {
            validInputfields = true;
            break;
         }
      }

      //   Empty dob
      if (!dob) validInputfields = true;

      //   Alert when 1 input is empty
      if (validInputfields) alert('All fields must not empty');

      return validInputfields;
   }

   checkValidPhone(phone: string) {
      let validPhoneNumber = true;

      if (!(phone.length <= 15)) validPhoneNumber = false;

      if (!validPhoneNumber) alert('Phone number must less or equal to 15 characters');
      return validPhoneNumber;
   }

   checkValidEmail(email: string): boolean {
      let validEmail = true;

      if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) validEmail = false;

      if (validEmail === false) alert('Email is not a valid email');

      return validEmail;
   }
}
