/* eslint-disable */
import axios, { AxiosResponse } from 'axios';

/**
 * [This (abstract) class contains the async HTTP methods.
 * Extends from this class to use request methods]
 */

export default abstract class WebService {
   //
   private readonly HttpClient = axios.create({
      baseURL: 'https://online-learning-platform.conveyor.cloud',

      headers: {
         'Content-Type': 'application/json',
      },
   });

   /**
    * [GET method for requesting data (async function)]
    * @param  {[string]} URL [Indicate URL to send request - Example: '/User/settings']
    * @return {[Promise<any>]}      [Return default JSON Object using JSON.parse(responseData)]
    */
   async getMethod(URL: string): Promise<any> {
      let JSONString = '';

      await this.HttpClient.get(URL.trim())

         .then((response) => {
            this.consoleLogResponse(response);

            JSONString = JSON.stringify(response.data);
         })

         .catch((error) => {
            this.consoleLogError(error);
         });

      return JSON.parse(JSONString);
   }

   /**
    * [POST method for sending data to server (async function)]
    * @param  {[string]} URL [Indicate URL to send request - Example: '/User/Register']
    * @param  {[any]} dataToSend [Data to send server (will be converted to JSON string) ]
    *
    * @return {[Promise<any>]}      [Return default JSON Object using JSON.parse(responseData)]
    */
   async postMethod(URL: string, dataToSend: any): Promise<any> {
      let JSONString = '';

      await this.HttpClient.post(URL.trim(), JSON.stringify(dataToSend))

         .then((response) => {
            this.consoleLogResponse(response);
            JSONString = JSON.stringify(response.data);
         })

         .catch((error) => {
            this.consoleLogError(error);
         });

      return JSON.parse(JSONString);
   }

   /**
    * [PUT method for sending data to server to update record (async function)]
    * @param  {[string]} URL [Indicate URL to send request - Example: '/User/Register']
    * @param  {[any]} dataToSend [Pass a data to server ]
    *
    * @return {[Promise<any>]}      [Return default JSON Object]
    */
   async putMethod(URL: string, dataToSend: any): Promise<any> {
      let JSONString = '';

      await this.HttpClient.put(URL.trim(), JSON.stringify(dataToSend))

         .then((response) => {
            this.consoleLogResponse(response);
            JSONString = JSON.stringify(response.data);
         })

         .catch((error) => {
            this.consoleLogError(error);
         });

      return JSON.parse(JSONString);
   }

   /*Debug functions (For debug purpose)*/

   private consoleLogResponse(response: AxiosResponse): void {
      console.log('----------------- Response from server: -----------------');
      console.log('Response status: ' + response.status);
      console.log('Response text: ' + response.statusText);
      console.log('Response header: ' + response.headers);
      console.log('Response config: ' + response.config);
      console.log('Response request: ' + response.request);
      console.log('Response data: ' + response.data);
   }

   private consoleLogError(error: any): void {
      console.log('----------------- Something went wrong-----------------');
      console.error(error);
   }
}
