/* eslint-disable */
import Vue from 'vue';
import accountService from '@/service/AccountService';

export default class LoginViewModel {
   //

   usernameOrEmail = '';
   password = '';
   rememberMe = false;

   //
   login(): void {
      const loginValidation = new LoginValidation();

      this.usernameOrEmail = this.usernameOrEmail.trim();
      this.password = this.password.trim();
      //   No input fields are ampty
      if (!loginValidation.checkEmptyInputFields(this.usernameOrEmail, this.password)) {
         //
         // Validate password
         if (loginValidation.validPassword(this.password)) {
            //
            this.setCookie();

            accountService.login({ UserNameOrEmail: this.usernameOrEmail, Password: this.password });
         }
      }
   }

   setCookie(): void {
      Vue.$cookies.remove('username');
      Vue.$cookies.remove('password');

      // Checkbox rememberMe is checked -> Set cookies
      if (this.rememberMe) {
         Vue.$cookies.set('username', this.usernameOrEmail);
         Vue.$cookies.set('password', this.password);

         Vue.$cookies.config(new Date('07/13/2022 04:35:00 PM').toLocaleString()); // Expire date is 7 days
      }
   }

   getCookie(): void {
      const usernameCookie: string = Vue.$cookies.get('username');
      const passwordCookie: string = Vue.$cookies.get('password');

      if (usernameCookie && passwordCookie) {
         this.usernameOrEmail = usernameCookie;
         this.password = passwordCookie;
         this.rememberMe = true;
      }
   }
}

class LoginValidation {
   checkUserIsLoggedIn(): boolean {
      // check user token exists on cookie browser
      if (Vue.$cookies.get('token')) {
         alert('You are already logged in');
         return true;
      }
      return false;
   }

   checkEmptyInputFields(username: string, password: string): boolean {
      // All fields are not empty
      if (username && password) return false;

      alert('All input fields must not empty');
      return true;
   }

   validPassword(password: string): boolean {
      // Check password length
      if (password.length < 8 || password.length > 31) {
         alert('Password must in range [8-31]');
         return false;
      }

      return true;
   }
}
