/* eslint-disable */
import router from '@/router';
import Vue from 'vue';

import WebService from './WebService';

class AccountService extends WebService {
   constructor() {
      super();
   }

   async login(account: object): Promise<void> {
      const user: { token: string; expiration: string; user: object } = await this.postMethod('/User/Login', account);

      Vue.$cookies.set('token', user.token, new Date(user.expiration));

      sessionStorage.setItem('userLoginSession', JSON.stringify(user.user));
      sessionStorage.setItem('userToken', user.token);
      router.push('/');
   }

   register(account: object) {
      this.postMethod('/User/Register', account);
   }

   async updateUserProfile(user: object) {
      await this.putMethod('/User/Login', user);
   }
}

const accountService = new AccountService();
export default accountService;
